/* BUTTONS CONTACTS SECTION */
.contacts__item--button {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

@media screen and (min-width:768px) {
    .contacts__item--button {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 14px;
        justify-content: center;
    }
}

@media screen and (min-width: 1200px) {
    .contacts__item--button {
        gap: 15px;
    }
}

.contacts__item--button-first {
    font-size: 16px;
    font-weight: 700;
    line-height: 1.6;
    width: 161px;
    border: 0;
    color: $color-first;
    background-color: $color-twelfth;
    padding: 10px 19px 10px 17px;
    border-radius: 22px;
    align-items: baseline;
    // margin: 34px 0 10px 79px;

    &:hover,
    &:focus {
        box-shadow: 0px 4px 4px 0px #00000040;
    }
}

@media screen and (min-width: 768px) {
    .contacts__item--button-first {
        width: 191px;
        padding-left: 35px;
        padding-right: 30px;
    }
}
@media screen and (min-width: 1200px) {
    .contacts__item--button-first {
        width: 198px;
        padding-right: 35px;
    }
}

.contacts__item--button-second {
    font-size: 16px;
    font-weight: 700;
    line-height: 1.6;
    width: 161px;
    border: 0;
    color: $color-twelfth;
    background-color: $color-first;
    padding: 10px 19px 10px 17px;
    border-radius: 22px;
    align-items: baseline;

    &:hover,
    &:focus {
        background-color: $color-fifth;
        box-shadow: 0px 4px 4px 0px #00000040;
        transform: var(--t-duration), var(--t-timing-function);
    }
}
@media screen and (min-width: 768px) {
    .contacts__item--button-second {
        width: 191px;
        padding-left: 35px;
        padding-right: 30px;
    }
}
@media screen and (min-width: 1200px) {
    .contacts__item--button-second {
        width: 198px;
        padding-right: 35px;
    }
}

.contacts__item--button-icon {
    width: 3.6px;
    height: 8px;
    position: absolute;
    margin-left: 8px;
    margin-top: 10px;
    color: $color-first;
}
@media screen and (min-width: 768px) {
    .contacts__item--button-icon {
        width: 4.3px;
        margin-left: 13px;
    }
}

.contacts__item--button-icon:last-child {
    color: currentColor;
}